.exchange_counter_container {
  background: #fff;
  padding: 10px;
  border-radius: 5px;
  height: calc(100vh - 70px);
  display: flex;
  flex-direction: column;

  .exchange_table {
    flex: 1;
  }

  .exchange_pagination {
    padding: 10px 10px 0 0;
    display: flex;
    justify-content: flex-end;
  }
}
