.layout_header {
  width: 100vw;
  height: 60px;
  background: rgba(#ccc, .5);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;

  .header_left {
    font-size: 20px;
    cursor: pointer;
  }

  .header_center {
    font-size: 20px;
    font-weight: 600;
  }
}
