.login_page {
  width: 100vw;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to top left, rgb(201, 103, 103), transparent),
    linear-gradient(to top right, rgb(66, 66, 226), transparent),
    linear-gradient(to bottom right, rgb(36, 156, 36), transparent),
    linear-gradient(to bottom left, rgb(182, 182, 97), transparent);
  background-repeat: no-repeat;

  .login_container {
    width: 600px;
    height: 280px;
    padding: 5px 50px;
    background: rgba(255, 255, 255, .4);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;

    form {
      width: 100%;
    }
  }
}
