.checkout_counter_container {
  background: #fff;
  padding: 10px;
  border-radius: 5px;
  height: calc(100vh - 70px);
  display: flex;
  justify-content: space-between;

  .checkout_counter_left {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 5px;

    .checkout_counter_timenumber {
      display: flex;
      justify-content: space-between;
      line-height: 40px;
      margin-bottom: 10px;
      font-size: 24px;
      white-space: nowrap;

      p {
        padding: 0;
        margin: 0;
      }
    }


  }

  .checkout_counter_right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 30%;
    height: 100%;
    background: #fff;
    padding: 0 15px;
  }
}
