.page_home {
  width: 100vw;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to top left, rgb(201, 103, 103), transparent),
    linear-gradient(to top right, rgb(66, 66, 226), transparent),
    linear-gradient(to bottom right, rgb(36, 156, 36), transparent),
    linear-gradient(to bottom left, rgb(182, 182, 97), transparent);
  // background-size: 50% 50%;
  background-repeat: no-repeat;

  .home_top,
  .home_buttom {
    display: flex;
    width: 70vw;
    justify-content: space-between;

    .home_item {
      width: 25%;
      height: 210px;
      margin: 25px 20px;
      // background: #0092ff;
      background-color: rgba(#0092ff, 0.8);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 20px;
      font-weight: 600;
      border-radius: 10px;
      color: #fff;
      transition: transform 0.3s ease-in-out;

      .homt_item_title {
        margin-top: 15px;
      }

      &:hover {
        cursor: pointer;
        transform: scale(1.1);
      }
    }

    .item_pay {
      width: 40%;
      background: #ffc53d;
    }

    .item_logout {
      background: #ff7a45;
    }

    .item_setting {
      background: #bfbfbf;
    }

    .item_report {
      background: #5cdbd3;
    }

  }


